.mainWrapper {
    width: 100%;
//    width: 1920px;
    background: rgb(122, 15, 33);

    .appWrapper {
        
        width: 100%;
//        width: 820px;
        margin: auto;
//        background: gray;

        .app {
        //    width: 100%;
            width: 560px;
            padding: 50px 0;
            margin: auto;

            .headerWrapper {
                display: flex;
                justify-content: center;
                width: 375px;
                height: 50px;
                margin: 0 0 0 136px;
                border: 1px solid black;

                .header {
                    margin: auto;
                    font-size: 28px;
                    font-weight: bold;
                }
            }

            .content {
                margin: auto;
                display: flex;
                flex-direction: row;
                justify-content: space-evenly;

                .listWrapper {
                    .pokemonList {
                        width: 340px;
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        margin-top: 10px;
                    }
                }

                .pokeFilter{
                    height: 30px;
                    width: 150px;
                //    border: 1px solid black;
                    margin-left: 357px;
                    position: absolute;
                }

                .pokemonInfo {
                    width: 180px;
                    height: 385px;
                    margin: 140px 0 0 27px;

                }
            }
        }
    }
}

